var mytimer = 0;
export function delay(callback, ms) {
  return function () {
    var context = this;
    var args = arguments;
    clearTimeout(mytimer);
    mytimer = setTimeout(function () {
      callback.apply(context, args);
    }, ms || 0);
  };
}
export function addCustomEventListener(selector, event, handler) {
  var rootElement = document.querySelector('body');
  rootElement.addEventListener(event, function (evt) {
    var targetElement = evt.target;
    while (targetElement != null) {
      if (targetElement.matches(selector)) {
        handler(evt, targetElement);
        return;
      }
      targetElement = targetElement.parentElement;
    }
  }, true);
}
export function humanFileSize(bytes) {
  var approx = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : false;
  var si = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : false;
  var dp = arguments.length > 3 && arguments[3] !== undefined ? arguments[3] : 1;
  var thresh = si ? 1000 : 1024;
  if (Math.abs(bytes) < thresh) {
    return bytes + ' B';
  }
  var units = si ? ['kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'] : ['KiB', 'MiB', 'GiB', 'TiB', 'PiB', 'EiB', 'ZiB', 'YiB'];
  var u = -1;
  var r = Math.pow(10, dp);
  do {
    bytes /= thresh;
    ++u;
  } while (Math.round(Math.abs(bytes) * r) / r >= thresh && u < units.length - 1);
  if (approx) {
    return Math.floor(bytes) + ' ' + units[u];
  } else {
    return bytes.toFixed(dp) + ' ' + units[u];
  }
}
export function colorOpacity(hexColor, opacity) {
  // validate hex string
  var hex = String(hexColor).replace(/[^0-9a-f]/gi, '');
  if (hex.length < 6) {
    hex = hex[0] + hex[0] + hex[1] + hex[1] + hex[2] + hex[2];
  }
  var dec = {
    r: parseInt(hex.substr(0, 2), 16),
    g: parseInt(hex.substr(2, 2), 16),
    b: parseInt(hex.substr(4, 2), 16)
  };
  return "rgba(".concat(dec.r, ",").concat(dec.g, ",").concat(dec.b, ",").concat(opacity, ")");
}
export function colorLuminance(hexColor, lumModifier) {
  // validate hex string
  var hex = String(hexColor).replace(/[^0-9a-f]/gi, '');
  if (hex.length < 6) {
    hex = hex[0] + hex[0] + hex[1] + hex[1] + hex[2] + hex[2];
  }
  var lum = lumModifier || 0;

  // convert to decimal and change luminosity
  var rgb = '#';
  for (var i = 0; i < 3; i++) {
    var c = parseInt(hex.substr(i * 2, 2), 16);
    c = Math.round(Math.min(Math.max(0, c + c * lum), 255)).toString(16);
    rgb += ('00' + c).substr(c.length);
  }
  return rgb;
}
export function getElemTypeClass(elem) {
  if (elem.type === 'directory') {
    return 'folder.svg';
  } else {
    var mime = elem.mime;
    if (mime.match(/^video\//)) {
      return 'video.svg';
    } else if (mime === 'text/calendar') {
      return 'text-calendar.svg';
    } else if (mime === 'text/csv' || mime.match(/^application\/.*opendocument\.spreadsheet$/) || mime.match(/^application\/.*office.*sheet$/)) {
      return 'x-office-spreadsheet.svg';
    } else if (mime.match(/^text\//)) {
      return 'text.svg';
    } else if (mime.match(/^application\/pdf$/)) {
      return 'application-pdf.svg';
    } else if (mime.match(/^application\/gpx/)) {
      return 'location.svg';
    } else if (mime.match(/^image\//)) {
      return 'image.svg';
    } else if (mime.match(/^audio\//)) {
      return 'audio.svg';
    } else if (mime.match(/^application\/.*opendocument\.text$/) || mime.match(/^application\/.*word.*document$/)) {
      return 'x-office-document.svg';
    } else if (mime.match(/^application\/.*opendocument\.presentation$/) || mime.match(/^application\/.*office.*presentation$/)) {
      return 'x-office-presentation.svg';
    }
    return 'file.svg';
  }
}